@import '_variables.scss';
@import '_fonts.scss';
body{
    font-family: $Poppins-Regular;
    font-size: 16px;
    color: $c-black;
}
/*colors*/
.c-pink{
    color: $c-pink;
}
.c-white{
    color: $c-white;
}
.bg-black{
    background-color: $c-black;
}
.bg-pink{
    background-color: $c-pink;
}
/*End colors*/

/*custom padding*/
.p-t-100{
    padding-top: 100px;
}
.p-b-100{
    padding-bottom: 100px;
}
.p-y-100{
    padding-top: 100px;
    padding-bottom: 100px;
}
/*custom padding*/
.sect-tb{
    padding-bottom: 100px;
    padding-top: 100px;
}
.sub-title{
    font-family: $Poppins-Bold;
    font-size: 56px;
    line-height: 76px;
}
.cust-btn{
    text-decoration: none;
    color: $c-white;
}
.desc{
    font-size: 18px;
    line-height: 36px;
}
.small-title-txt{
    color: $c-pink;
    margin-bottom: 20px;
}
.about-sect{
    background-color: $c-black;
}
.technologies-sect{
    position: relative;
    z-index: 0;
    background-color: $c-l-gray2;
    &:after{
        content: '';
        position: absolute;
        background-image: url(../../assets/img/technologies-bg.jpg);
        top: 0;
        width: calc(100% - 50.65%);
        height: 100%;
        right: 0;
        z-index: -1;
        background-size: cover;
        background-position: center;
    }
    .global-box{
        width: 342px;
        height: 342px;
    }
    .cust-btn{
        width: 342px;
        text-decoration: none;
        &:hover{
            color: $c-white;
        }
    }
}
.owl-carousel{
    .owl-item {
        img{
            width: auto;
            margin: 0 auto;
        }
    }
    .owl-dots{
        margin: 6px auto 0;
        text-align: center;
        .owl-dot{
            width: 6px;
            height: 6px;
            background-color: $c-l-gray !important;
            border-radius: 50px;
            margin: 0 3px;
            &.active{
                width: 30px;
                background-color: $c-pink !important;
            }
        }
    }
}
.industries-box{
    border-radius: 20px;
    .small-title-txt{
        font-family: $Poppins-SemiBold;
        font-size: 32px;
        margin-bottom: 15px;
        color: $c-white;
    }
    .inner-box{
        background-color: rgba(225,0,91,.8);
        opacity: 0;
        transition: ease-in-out .3s;
    }
    a{
        &:hover .inner-box{
            opacity: 1;
            transition: ease-in-out .3s;
        }
    }
}

/*Responsive*/
@media(min-width:1400px){
    .container{
        max-width: 1440px;
    }
}
/*Responsive*/